@font-face {
    font-family: 'Press Start 2p';
}

body{
    margin: 0;
    height: 100vh;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-family: 'Press Start 2p', sans-serif;
    font-size: 1.45vh;
    background-color: #454545;
}

#root{
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}



@media (max-aspect-ratio: 320/700) {
    body{
        font-size: 1vh;
    }
}
.game-box{
    min-width: 250px;
    background-color: #83cd2b;
    padding: 1.5em;
    box-shadow: inset 0 0 2em 0.75em #c1ff3d;
    border-radius: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-grow: 0;
            flex-grow: 0;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}


.game{
    padding-top: 5px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.game-grid{
    height: 30em;
    border: 8px solid #222222;
    border-radius: 20px;
    padding: 2em 0.5em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-grow: 0;
            flex-grow: 0;
    background-color: lightgray;
}

.centeredgame{
    padding-left: 0.5em;
    padding-right: 0.5em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}

.game-grid .right-info{
    color: #111111;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding-right: 0.05em;
    text-align: left;
    font-size: 0.8em;
    width: 6.5em;
}

.game-grid .left-info{
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    -webkit-flex-grow: 2;
            flex-grow: 2;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    color: #111111;
    width: 6.5em;
}

.game-grid .left-info div:first-child{
    padding-bottom: 0.75em;
}

.game-grid .left-info * {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-grow: 0;
            flex-grow: 0;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}


@media screen and (max-width: 500px) {
    .game{
        padding: 0;
        width: 100%;
        height: 100%;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .game-box{
        height: 100%;
        background-color: #83cd2b;
        padding: 1em;
        box-shadow: inset 0 0 2em 0.75em #c1ff3d;
        border-radius: 0;
        -webkit-justify-content: space-around;
                justify-content: space-around;
    }
}
b {
    border-right: 1px solid #111111;
    border-top: 1px solid #111111;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    display: inline-block;
}

.grid{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}
.grid-row{
    margin: 0;
    height: 1.5em;
}

p :first-child{
    border-left: 1px solid #111111;
}

p:last-child b{
    border-bottom: 1px solid #111111;
}

.white {
    background-color: aliceblue;
}

.black{
    background-color: #828282;
    outline: 0.075em solid #e8e8e8;
    outline-offset: -3px;
}
.green {
    background-color: #2ECC40;
    outline: 0.075em solid #e8e8e8;
    outline-offset: -3px;
}
.blue {
    background-color: #0074D9;
    outline: 0.075em solid #e8e8e8;
    outline-offset: -3px;
}
.orange {
    background-color: #FF851B;
    outline: 0.075em solid #e8e8e8;
    outline-offset: -3px;
}
.purple {
    background-color: #B10DC9;
    outline: 0.075em solid #e8e8e8;
    outline-offset: -3px;
}
.cyan {
    background-color: MediumTurquoise;
    outline: 0.075em solid #e8e8e8;
    outline-offset: -3px;
}
.yellow {
    background-color: #f5cf00;
    outline: 0.075em solid #e8e8e8;
    outline-offset: -3px;
}
.red {
    background-color: #FF4136;
    outline: 0.075em solid #e8e8e8;
    outline-offset: -3px;
}
.controls-box{
    -webkit-flex-grow:0;
            flex-grow:0;
    padding-top: 2em;

}

.controls{
    border: .1em solid #111111;
    border-radius: 10px;
    box-shadow: inset 0.05em 0.05em 0.20em 0.05em #2b3703;
    padding: 1em 0.5em 2em;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-grow: 0;
            flex-grow: 0;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: greenyellow;
}

.row{
    display: -webkit-flex;
    display: flex;
    margin-top: 1em;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.video-game-button {
    text-shadow: 1px 1px #696969, -1px -1px #282828;
    line-height: 5em;
    text-align: center;
    display: inline-block;
    width: 5em;
    border-radius: 3em;
    background-color: grey;
    box-shadow: 0 .3em #242424;
    color: #343434;
    margin: 5px;
    background-image: -webkit-linear-gradient(left top, #e0e0e0 3%, #696969 22%, #282828 99%);
    background-image: linear-gradient(to right bottom, #e0e0e0 3%, #696969 22%, #282828 99%);
    cursor: pointer;
    padding-left: 5px;
}
.start-btn{
    font-size: 1rem;
    text-align: center;
    display: inline-block;
    margin-top: 0.1em;
    margin-bottom: 0.1em;
    font-weight: bold;
    padding: 0.3em 0.3em 0.3em 0.3em;
    background-color: lightgray;
    text-shadow: -1px -1px black, 1px 1px white;
    color: gray;
    border-radius: 7px;
    box-shadow: 0 .2em gray;
    cursor: pointer;
}
.video-game-button:active, .start-btn:active {
    box-shadow: none;
    position: relative;
    top: .2em;
}

.video-game-button.active {
    box-shadow: none;
    position: relative;
    top: .2em;
}
